import React from "react";
import { Button } from "@material-ui/core";
const DialogComponent = props => {
  return (
    <div>
      <h1 style={{ textAlign: "center" }}>Lab 10</h1>
      <Button
        color="primary"
        variant="raised"
        onClick={() => props.childdata("some data from dialog")}
      >
        Send data to parent
      </Button>
    </div>
  );
};
export default DialogComponent;
