import React from "react";
import { MuiThemeProvider } from "@material-ui/core/styles";
import { Card, CardContent } from "@material-ui/core";
import theme from "./theme";
import UserBubbleList from "./userbubblelist";
import TopBar from "./topbar";
import { Dialog, DialogContent } from "@material-ui/core";
import DialogComponent from "./DialogComponent";

import "../../App.css";
class Exercise5 extends React.PureComponent {
  state = {
    users: [],
    open: false,
    childData: ""
  };

  handleChildSentData(somevalue) {
    this.setState({ childData: somevalue });
  }

  handleOpenDialog = () => this.setState({ open: true });
  handleCloseDialog = () => this.setState({ open: false });
  async componentDidMount() {
    let response = await fetch("http://localhost:5000/users");
    let payload = await response.json();
    this.setState({ users: payload });
  }
  render() {
    const { users } = this.state;
    return (
      <MuiThemeProvider theme={theme}>
        <div>
          <TopBar viewDialog={this.handleOpenDialog} />
          <Dialog
            open={this.state.open}
            onClose={this.handleCloseDialog}
            style={{ margin: 20 }}
          >
            <DialogContent>
              <DialogComponent
                childdata={someDataFromChild =>
                  this.handleChildSentData(someDataFromChild)
                }
              />
            </DialogContent>
          </Dialog>
        </div>
        <Card
          style={{
            marginTop: "10%",
            marginLeft: "10%",
            marginBottom: "10%",
            width: "80%",
            height: "100%"
          }}
        >
          <CardContent style={{ textAlign: "center" }}>
            <h5> {this.state.childData}</h5>
            <div className="usersList">
              <UserBubbleList users={users} />
            </div>
          </CardContent>
        </Card>
      </MuiThemeProvider>
    );
  }
}
export default Exercise5;
