import React from "react";
import io from "socket.io-client";
import { MuiThemeProvider } from "@material-ui/core/styles";
import {
  Button,
  TextField,
  Dialog,
  DialogContent,
  Card,
  CardContent,
  Typography,
  ListItem
} from "@material-ui/core";
import theme from "../theme";
import "../App.css";
import TopBar from "./topbar";
import DialogComponent from "./DialogComponent";
import UserBubbleList from "./userbubblelist";
import logo from "./chat.png"; // Tell Webpack this JS file uses this image

import Radio from "@material-ui/core/Radio";
import { List } from "@material-ui/core";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import PersonPinCircle from "@material-ui/icons/PersonPinCircle";
import ChatBubble from "@material-ui/icons/ChatBubble";

class WhatsupChat extends React.PureComponent {
  state = {
    socket: null,
    messages: [],
    chatName: "",
    roomName: "",
    msg: "",
    isTyping: false,
    hideJoinObjects: false,
    NameErrorText: "Unique name required",
    errorText: "Unique name required",
    RoomErrorText: "Enter a room name",
    open: false,
    rooms: [],
    UsersAvailable: [],
    isJoined: false,
    showMsgErr: true
  };

  handleOpenDialog = () => this.setState({ open: true });
  handleCloseDialog = () => this.setState({ open: false });

  handleRoomButtonsChange = event => {
    this.setState({ roomName: event.target.value, RoomErrorText: "" });
  };

  // handler for Join button click
  handleJoin = () => {
    this.setState({
      errorText: "Message required",
      isJoined: true
    });

    this.state.socket.emit("join", {
      chatName: this.state.chatName,
      roomName: this.state.roomName
    });
  };
  // handler for name TextField entry
  onNameChange = e => {
    if (e.target.value === "") {
      this.setState({
        chatName: e.target.value,
        NameErrorText: "Unique name required"
      });
    } else {
      this.setState({
        chatName: e.target.value,
        NameErrorText: ""
      });
    }
  };

  // handler for name TextField entry
  onRoomChange = e => {
    if (e.target.value === "") {
      this.setState({
        roomName: e.target.value,
        RoomErrorText: "Enter a room name"
      });
    } else {
      this.setState({
        roomName: e.target.value,
        RoomErrorText: ""
      });
    }
  };

  // handler for message TextField entry
  onMessageChange = e => {
    this.setState({ msg: e.target.value });
    if (e.target.value === "") {
      this.setState({ errorText: "Message required", showMsgErr: true });
    } else {
      this.setState({ errorText: "" });
      if (this.state.isTyping === false) {
        this.state.socket.emit("typing", { from: this.state.chatName });
        this.setState({ isTyping: true });
      }
    }
  };

  // generic handler for all 3 scenario #1 messages:
  // if state update is derived from data that is already in state
  // it is best practice to give a function to setState that uses
  // the previous state and returns the state update
  addMessage = dataFromServer => {
    this.setState({ errorText: "Message required", showMsgErr: true });

    this.setState(previousState => {
      let messages = [...previousState.messages];
      messages.push(dataFromServer);
      return { messages };
    });
  };

  typingHandle = dataFromServer => {
    this.setState({ errorText: dataFromServer.text, showMsgErr: false });
  };
  nameExists = dataFromServer => {
    this.setState({ NameErrorText: "Name exists", isJoined: false });
  };

  onWelcome = dataFromServer => {
    this.addMessage(dataFromServer);
    this.setState({ hideJoinObjects: true });
  };

  // handler for send message button
  handleSendMessage = e => {
    this.setState({
      errorText: "Message required"
    });
    e.preventDefault();
    this.state.socket.emit(
      "message",
      { from: this.state.chatName, text: this.state.msg },
      err => {}
    );
    this.setState({ msg: "", isTyping: false });
  };

  componentDidMount = () => {
    // connect to sserver locally
    //const socket = io.connect("localhost:5000", { forceNew: true });

    // connect to server on Heroku cloud
    const socket = io.connect();

    this.setState({ socket: socket });
    // register scenario1 event handlers
    socket.on("welcome", this.onWelcome);
    socket.on("someoneistyping", this.typingHandle);
    socket.on("nameexists", this.nameExists);
    socket.on("someonejoined", this.addMessage);
    socket.on("someoneleft", this.addMessage);
    socket.on("newmessage", this.addMessage);
    socket.on("rooms", dataFromServer => {
      this.setState({ rooms: dataFromServer });
    });

    socket.on("UsersAvailable", dataFromServer => {
      this.setState({ UsersAvailable: dataFromServer });
    });
    socket.emit("getRooms");
  };

  render() {
    const {
      messages,
      chatName,
      roomName,
      hideJoinObjects,
      msg,
      showMsgErr
    } = this.state;
    return (
      <MuiThemeProvider theme={theme}>
        <div>
          <TopBar
            viewDialog={this.handleOpenDialog}
            isJoined={this.state.isJoined}
          />
          <Dialog
            open={this.state.open}
            onClose={this.handleCloseDialog}
            style={{ margin: 20 }}
          >
            <DialogContent>
              <Typography variant="h7" color="primary" gutterBottom>
                Currently online users
              </Typography>
              <List>
                {this.state.UsersAvailable.map((user, index) => (
                  <ListItem key={index}>
                    <section style={{ marginRight: "auto" }}>
                      <PersonPinCircle
                        style={{
                          color: user.color,
                          height: 30,
                          width: 30,
                          alignContent: "left",
                          float: "left"
                        }}
                      />
                    </section>
                    {user.name} is in {user.room} room
                  </ListItem>
                ))}
              </List>
            </DialogContent>
          </Dialog>
        </div>

        <div style={{ display: "flex", justifyContent: "center" }}>
          {/* <img src={logo} alt="Chat Logo" height="100" width="100" />  */}
          <ChatBubble
            style={{
              color: "#3f51b5",
              height: 100,
              width: 100
            }}
          />
        </div>

        {!hideJoinObjects && (
          <React.Fragment>
            <Card
              style={{
                marginTop: "10%",
                marginLeft: "10%",
                marginBottom: "10%",
                width: "80%",
                height: "100%"
              }}
            >
              <CardContent style={{ textAlign: "center" }}>
                {!hideJoinObjects && (
                  <React.Fragment>
                    <TextField
                      onChange={this.onNameChange}
                      placeholder="Enter unique name"
                      autoFocus={true}
                      required
                      value={chatName}
                      helperText={this.state.NameErrorText}
                      error={
                        this.state.NameErrorText.length === 0 ? false : true
                      }
                    />
                    <br />
                  </React.Fragment>
                )}
              </CardContent>
            </Card>

            <Card
              style={{
                marginTop: "10%",
                marginLeft: "10%",
                marginBottom: "10%",
                width: "80%",
                height: "100%"
              }}
            >
              <CardContent style={{ textAlign: "center" }}>
                <Typography color="primary" gutterBottom>
                  join excisting or a new room
                </Typography>

                <RadioGroup
                  aria-label="Rooms"
                  name="Rooms"
                  value={this.state.roomName}
                  onChange={this.handleRoomButtonsChange}
                >
                  {this.state.rooms.map((room, index) => (
                    <FormControlLabel
                      value={room.room}
                      control={<Radio />}
                      label={`${room.room}    | joined: ${room.count}`}
                      key={index}
                    />
                  ))}
                </RadioGroup>

                {!hideJoinObjects && (
                  <React.Fragment>
                    <TextField
                      onChange={this.onRoomChange}
                      placeholder="Enter a room name"
                      autoFocus={true}
                      required
                      value={roomName}
                      helperText={this.state.RoomErrorText}
                      error={
                        this.state.RoomErrorText.length === 0 ? false : true
                      }
                    />
                    <br />
                  </React.Fragment>
                )}
              </CardContent>
            </Card>

            <Button
              onClick={this.handleJoin}
              color="primary"
              variant="contained"
              disabled={
                this.state.roomName.length === 0 ||
                this.state.chatName.length === 0
                  ? true
                  : false
              }
              style={{ marginTop: "1em" }}
            >
              Join
            </Button>
          </React.Fragment>
        )}

        {hideJoinObjects && (
          <React.Fragment>
            <TextField
              onChange={this.onMessageChange}
              placeholder="type something here"
              autoFocus={true}
              required
              helperText={this.state.errorText}
              error={
                this.state.errorText.length === 0 ? false : true && showMsgErr
              }
              value={msg}
            />
            <br />
            <Button
              onClick={this.handleSendMessage}
              color="primary"
              variant="contained"
              disabled={this.state.errorText}
              style={{ marginTop: "1em" }}
            >
              Send
            </Button>
          </React.Fragment>
        )}

        <h5> {this.state.childData}</h5>
        <div className="usersList">
          <UserBubbleList users={messages} myChatName={chatName} />
        </div>
      </MuiThemeProvider>
    );
  }
}
export default WhatsupChat;
