import React from "react";
import ReactDOM from "react-dom";
import { ListItem } from "@material-ui/core";
import Bubble from "./bubble";
import Triangle from "./triangle";
class UserBubble extends React.Component {
  componentDidMount = () => {
    let userDOM = ReactDOM.findDOMNode(this);
    userDOM.scrollIntoView({ block: "end", behavior: "smooth" });
    userDOM.blur();
  };
  render() {
    let color = "#1B5E20";
    if (this.props.myChatName === this.props.user.from)
      return (
        <div className="iClass">
          <ListItem
            ref="user"
            style={{ textAlign: "left", marginBottom: "5px" }}
          >
            <Bubble
              user={this.props.user}
              color={this.props.user.colour}
              myChatName="TEST_Bubble"
            />
            <Triangle color={this.props.user.colour} />
          </ListItem>
        </div>
      );
    else
      return (
        <div>
          <ListItem
            ref="user"
            style={{ textAlign: "left", marginBottom: "5px" }}
          >
            <Bubble
              user={this.props.user}
              color={this.props.user.colour}
              myChatName={this.props.myChatName}
            />
            <Triangle color={this.props.user.colour} />
          </ListItem>
        </div>
      );
  }
}
export default UserBubble;
