import React from "react";
import "../App.css";
const Bubble = props => {
  return (
    <div className="userBubble" style={{ background: props.user.colour }}>
      <span
        style={{
          fontSize: "smaller",
          fontWeight: "bold",
          alignContent: "left"
        }}
      >
        {props.user.from} says:
      </span>

      <div
        style={{
          fontSize: "smaller",
          fontWeight: "bold",
          alignContent: "right",
          float: "right"
        }}
      >
        room: {props.user.room}
        <br /> @:{props.user.time}
      </div>

      <br />
      <br />
      <div
        style={{
          margin: 10,
          fontSize: 25,
          fontWeight: "bold"
        }}
      >
        {props.user.text}
      </div>
    </div>
  );
};
export default Bubble;
